jQuery.fn.ratingTool = function( options ) { 
    // Bob's default settings:
    var defaults = {
        question: ['question-1', 'question-2'],
		qInstruction: ['instruction','instruction'],
		maxRate:5,
		statementText:['statement-1','statement-2', 'statement-3', 'statement-2'],
		ResulttextboxId: ['quest1','quest2']
    };
    var settings = $.extend( {}, defaults, options ); 
    return this.each(function() {        		
		var st = settings.statementText,
			qs = settings.question,
			mr = settings.maxRate,
			rtb = settings.ResulttextboxId,
			qins = settings.qInstruction;
		var stLoop='',
			qsloop = '',
			qiLoop = '',
			mxrt = '',
			mart = '',
			totalrat = '',
			questArray1 = [],
			questArray2 = [],
			result = '';
		
		//Question Heading
		
		console.log(qs.length)
		if(qs.length==1){
			for(j = 0; j < qs.length; j++ ){
						if(j==0){var colsp = 2;}else{var colsp = 1;}			
						qsloop += '<th colspan="'+colsp+'">';
						qsloop += qs[j];
						qsloop += '</th>';
			};
		
		}else{
			for(j = 0; j < qs.length; j++ ){
						if(j==0){var colsp = 2;}else{var colsp = 1;}			
						qsloop += '<th colspan="'+colsp+'">';
						qsloop += qs[j];
						if(j==0){
						qsloop += '<th class="bg-line"></th>'
						}
						qsloop += '</th>';
			};
			
		}
		
		
		
		//Question Instruction
		if(qs.length==1){
			for(qi = 0; qi < qins.length; qi++ ){
				if(qi==0){var colsp = 2;}else{var colsp = 1;}			
				qiLoop += '<th colspan="'+colsp+'" class="instruction">';
				qiLoop += qins[qi];
				qiLoop += '</th>';
			}

			
		}else{
			for(qi = 0; qi < qins.length; qi++ ){
				if(qi==0){var colsp = 2;}else{var colsp = 1;}			
				qiLoop += '<th colspan="'+colsp+'" class="instruction">';
				qiLoop += qins[qi];
				if(qi==0){
					qiLoop += '<th class="bg-line"></th>'
				}
				qiLoop += '</th>';
			}
			
		}
		
		
		
		
		
		//Rating loop
		for(m=1; m<mr+1; m++){				
			mart += '<td class="rating-btn">'+ m +'</td>';	
			
		}
		
		//Rating Display
		if(qs.length==1){
			for(r=0; r< qs.length; r++){
				totalrat += '<td>';
				totalrat += '<table class="table rating-no" data-que="'+r+'"><tr>';
				totalrat += '<td class="click-val" data-cid="cid-'+r+'"></td>';
				totalrat += '<td style="width:4px; padding:0;"></td>';
				totalrat +=	mart ;
				totalrat += '<td style="width:4px; padding:0;"></td>';
				totalrat += '<td class="noval-btn" data-vid="vid-'+r+'">?</td>';
				totalrat += '</tr></table>';
				totalrat += '</td>';
				
			}
			
		}else{
			
			for(r=0; r< qs.length; r++){
				totalrat += '<td>';
				totalrat += '<table class="table rating-no" data-que="'+r+'"><tr>';
				totalrat += '<td class="click-val" data-cid="cid-'+r+'"></td>';
				totalrat += '<td style="width:4px; padding:0;"></td>';
				totalrat +=	mart ;
				totalrat += '<td style="width:4px; padding:0;"></td>';
				totalrat += '<td class="noval-btn" data-vid="vid-'+r+'">?</td>';
				totalrat += '</tr></table>';
				totalrat += '</td>';
				if(r==0){
				totalrat += '<td class="bg-line"></td>'
				}
			}
		}
		
		// Final Display
		for (i = 0; i < st.length; i++) { 
			stLoop += '<tr class="strow" data-st="'+i+'">';			
			stLoop += '<td>'+st[i]+'</td>';			
			stLoop += totalrat;
			stLoop += '</tr>';
		}
		
		// Append html in selected div
		$(this).addClass('clearfix');
		$(this).find('thead').html('<tr>'+qsloop+'</tr><tr>'+qiLoop+'</tr>')
		$(this).find('tbody').html(stLoop);
		// append result inputbox
		$('body').append(result);
		
		// Each Loop For Rating box dispan
		$('.rating-no').each(function(index){
			var rtbtn = $(this).find('.rating-btn');
			var resetbtn = $(this).find('.noval-btn');
			// update value click on ration box
			rtbtn.click(function(){
				var stIndex = $(this).closest('.strow').attr('data-st');
				var qIndex = $(this).closest('table').attr('data-que');
				var ansVal = $(this).text()
				//update questArray
				outputValue()
				if(qIndex == 0){
					questArray1[stIndex] = ansVal;
					//$('#'+rtb[qIndex]).val(questArray1);
					//$('#q1').val(questArray1);
					
				}else{
					questArray2[stIndex] = ansVal;
					//$('#'+rtb[qIndex]).val(questArray2);
					//$('#q2').val(questArray1);
				}
				outputValue(questArray1,questArray2)
				
				$(this).prevAll('.click-val').text($(this).text())
				$(this).nextUntil('.noval-btn').removeClass('active');
				$(this).nextAll('.noval-btn').removeClass('novalActive')
				$(this).addClass('active').prevUntil('.click-val').addClass('active');
			})
			// hover evvect;
			rtbtn.hover(function(){
				$(this).addClass('hover').prevUntil('.click-val').addClass('hover');
			}, function(){
				$(this).removeClass('hover').prevUntil('.click-val').removeClass('hover');
			});
			// reset value click on question mark box
			resetbtn.click(function(){
				$(this).addClass('novalActive');
				$(this).prevAll().removeClass('active');
				$(this).prevAll('.click-val').text($(this).text())
				var stIndex = $(this).closest('.strow').attr('data-st');
				var qIndex = $(this).closest('table').attr('data-que');	
				//update questArray
				if(qIndex == 0){
					questArray1[stIndex] = '98';
					//$('#'+rtb[qIndex]).val(questArray1);
					$('#q1').val(questArray1);
				}else{
					questArray2[stIndex] = '98';
					//$('#'+rtb[qIndex]).val(questArray2);
					$('#q2').val(questArray1);
				}
				outputValue(questArray1,questArray2)
			});
		});		
    }); 
};